.description {
  margin: 43px 20px 45px 35px;

  @media (min-width: $tablet-width) {
    margin: 57px 35px 37px 57px;
  };

  @media (min-width: 1700px) {
    margin: 150px 250px 30px 30px;
  };
};

.description-title {
  margin: 0 0 12px;
  padding: 0;
  @include basic-font-regular;
  font-size: 30px;
  letter-spacing: 4px;
  color: $dark_grey;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin-bottom: 52px;
    font-size: 60px;
    letter-spacing: 6px;
  };

  @media (min-width: $desktop-width) {
    line-height: 70px;
  };
};

.description-text {
  margin: 0 0 40px 0;
  padding: 0;
  font-size: 18px;
  line-height: 51px;
  letter-spacing: 0.8px;
  color: $dark_grey;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
    font-size: 24px;
    letter-spacing: 0.6px;
  };
};

.description-add-text {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
    margin: 0 0 50px 0;
    font-size: 24px;
    line-height: 51px;
    letter-spacing: 0.6px;
  };
};

.description-signature {
  margin: 0 0 0 3px;
  padding: 0;
  @include basic-font-regular;
  font-size: 18px;

  @media (min-width: $tablet-width) {
    margin-left: 0;
    font-size: 36px;
  };
};
