.scroll-bottom {
  display: none;
  bottom: 0;
  text-align: center;

  @media (min-width: $tablet-width) {
    display: block;
  };
};

.scroll-top {
  display: none;

  @media (min-width: $desktop-width) {
    display: block;
    position: absolute;
    left: 42%;
    bottom: 53px;
    padding-left: 5px;
  };
};

.scroll-text {
  margin: 0;
  padding: 0 29px 24px 0;
  font-size: 18px;
  letter-spacing: 2px;
  color: $light_grey;
  text-transform: uppercase;

  &:hover {
    color: $hover-scroll;
  }

  @media (min-width: 1300px) {
    padding-right: 12px;
  };
};

.scroll-arrow {
  display: block;
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background-image: url('../../img/Double Arrow.png');
};