.nav {
  position: relative;
};

.nav-list {
  margin: 0;
  padding: 0;
  background-color: transparent;
  list-style: none;

  @media (min-width: $tablet-width) {
    z-index: 2;
    position: relative;
    display: flex;
    justify-content: space-between;
    padding-bottom: 36px;
    border-bottom: 1px solid $light_grey;
  };

  @media (min-width: $desktop-width) {
    padding-left: 558px;
    padding-right: 576px;
  };
};