.main-nav {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  };
};

.main-list {
  padding: 37px 60px 35px 42px;

  @media (min-width: $desktop-width) {
    padding-left: 622px;
    padding-right: 628px;
  };
};

.main-item {
  &:nth-child(3) {
    padding-right: 0;
  };

  &:last-child {
    padding-right: 27px;
  };
};

.main-link {
  @include nexia-font-normal;
  letter-spacing: 1px;
};
