.nav-item {
  margin: 0;
  padding: 5px 0;

  @media (min-width: $tablet-width) {
    padding: 0;

    &:first-child {
      padding-left: 30px;
    };

    &:last-child {
      padding-right: 30px;
    };

    &:nth-child(3) {
      padding-right: 12px;
    };
  };
};

.nav-link {
  @include basic-font-regular;
  font-size: 23px;
  letter-spacing: 1px;
  color: $light_grey;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    color: darkolivegreen;
  };

  @media (min-width: $tablet-width) {
    letter-spacing: 0;
  };
};
